<template>
  <div class="PM">
    <div class="header">
      <el-breadcrumb class="bread" separator-class="el-icon-arrow-right">
        <el-breadcrumb-item>业务管理</el-breadcrumb-item>
        <el-breadcrumb-item>推图</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="content">
      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <span>活动关卡|主线|自由关卡</span>
          <el-button size="mini" style="margin-left:10px;" type="primary" @click="handlePM">保存</el-button>
        </div>
        <div class="card-content">
          <table>
            <tr v-for="(item, index) in data" :key="index">
              <td class="tableHeader">
                <el-checkbox v-model="checkAll[index]" @change="handleCheckAllChange(index)">{{
                    item.children[0].label
                }}
                </el-checkbox>

              </td>
              <td v-if="index == 0" class="tableContent frist-table">
                <el-checkbox-group v-model="checkedList[index]">
                  <el-checkbox v-for="jtem in item.children[0].children" :label="jtem.label" :key="jtem.label">
                    {{ jtem.label }}
                  </el-checkbox>
                </el-checkbox-group>
              </td>
              <td v-if="index == 1" class="tableContent">
                <el-checkbox-group v-model="checkedList[index]">
                  <el-checkbox v-for="jtem in item.children[0].children" :label="jtem.label" :key="jtem.label">
                    {{ jtem.label }}
                  </el-checkbox>
                </el-checkbox-group>
              </td>
              <td v-if="index == 2" class="tableContent">
                <el-checkbox-group v-model="checkedList[index]">
                  <div class="check_group_box">
                    <div class="check_group">
                    <el-checkbox v-for="jtem in list3_1" :label="jtem" :key="jtem">
                      {{ jtem}}
                    </el-checkbox>
                  </div>
                  <div class="check_group">
                    <el-checkbox v-for="jtem in list3_2" :label="jtem" :key="jtem">
                      {{ jtem}}
                    </el-checkbox>
                  </div>
                  </div>
                  
                </el-checkbox-group>
              </td>
              <td v-if="index == 3" class="tableContent">
                <el-checkbox-group v-model="checkedList[index]">
                  <div class="check_group_box">
                    <div class="check_group">
                    <el-checkbox v-for="jtem in list4_1" :label="jtem" :key="jtem">
                      {{ jtem}}
                    </el-checkbox>
                  </div>
                  <div class="check_group">
                    <el-checkbox v-for="jtem in list4_2" :label="jtem" :key="jtem">
                      {{ jtem}}
                    </el-checkbox>
                  </div>
                  </div>
                  
                </el-checkbox-group>
              </td>
            </tr>

          </table>

        </div>
      </el-card>
    </div>
  </div>
</template>
  
<script>
const PMList = [
  "第一重门,第2重门,第3重门,第4重门",
  "寒露纪念关卡",
  "特异点F-东木,第一特异点-奥尔良,第二特异点-七丘之城,第三特异点-俄刻阿诺斯,第四特异点-伦敦,第五特异点-合众为一,第六特异点-卡美洛,第七特异点-巴比伦尼亚,终局特异点-所罗门,亚种特异点1-1.5-新宿,亚种特异点2-1.5-雅戈泰,亚种特异点3-1.5-下总国,亚种特异点4-1.5-塞勒姆,序／2018年 12月26日,2.1-永久冻土帝国,2.2-诸神黄昏,2.3-人智统合国,2.4-创世毁灭轮回,2.5-神代巨神海洋,2.6-星间都市山脉 奥林波斯,2.7-地狱界曼荼罗 平安京,Lostbelt No.6",
  "特异点F-东木,第一特异点-奥尔良,第二特异点-七丘之城,第三特异点-俄刻阿诺斯,第四特异点-伦敦,第五特异点-合众为一,第六特异点-卡美洛,第七特异点-巴比伦尼亚,亚种特异点1-1.5-新宿,亚种特异点2-1.5-雅戈泰,亚种特异点3-1.5-下总国,亚种特异点4-1.5-塞勒姆,2.1-永久冻土帝国,2.2-诸神黄昏,2.3-人智统合国,2.4-创世毁灭轮回,2.5-神代巨神海洋,2.6-星间都市山脉 奥林波斯,2.7-地狱界曼荼罗 平安京,Lostbelt No.6",
];
const listHeader = [
  "监狱塔内复仇鬼的哭泣",
  "迦勒底之门",
  "主线",
  "自由关卡",
];
const captial = [
  "活动关卡",
  "纪念关卡",
  "主线",
  "自由关卡",
];
export default {
  name: "PM",
  components: {},
  created() {

    for (let i = 0; i < PMList.length; i++) {
      let list = PMList[i].split(",");
      this.checkedOpt.push(list);
      let nodes = [];
      this.data.push({
        label: captial[i],
        children: []
      });
      for (let m = 0; m < list.length; m++) {
        nodes.push({
          label: list[m],
          children: []
        });
      }
      this.data[i].children.push({
        label: listHeader[i],
        children: nodes
      })
    }
  },
  watch: {},
  computed: {
  
  },
  data() {

    return {
      id: this.$route.query.id,
      checkAll: [
        false,
        false,
        false,
        false,
      ],
      checkedList: [[], [], [], []],
      checkedOpt: [],
      data: [],
      defaultProps: {
        children: 'children',
        label: 'label'
      },
      list1: PMList[0].split(','),
      list2: PMList[1].split(','),
      list3_1: PMList[2].split(',').slice(0, 9),
      list3_2: PMList[2].split(',').slice(9),
      list4_1: PMList[3].split(',').slice(0, 8),
      list4_2: PMList[3].split(',').slice(8),
    };
  },

  methods: {
    handleCheckAllChange(index) {
      this.checkedList[index] = this.checkAll[index] ? this.checkedOpt[index] : [];
    },
    handlePM() {
      let cheakList = this.checkedList;
      let data = [];
      for (let i = 0; i < cheakList.length; i++) {
        if (cheakList[i].length == 0) continue;
        data.push(cheakList[i].join(',') + '|' + listHeader[i]);
      }
      if (data.length == 0) return;
      let promiseArr = [];
      for (let i = 0; i < data.length; i++) {
        const params = {
          orderId: this.id,
          boostingTask: data[i],
          progress:"jsons",
          businessType: "BoostingLevels"
        };
        promiseArr.push(this.$api.taskBoosting(params));

      }
      Promise.all(promiseArr).then((res) => {
        for (let i = 0; i < res.length; i++) {
          const body = res[i].data;
          if (body.code != "200") {
            this.$message.error(body.message);
          }
        }
        this.$message.success("成功！");
      });
    },
  },
};
</script>

