<template>
  <div class="GreenSquare">
    <div class="header">
      <el-breadcrumb class="bread" separator-class="el-icon-arrow-right">
        <el-breadcrumb-item>业务管理</el-breadcrumb-item>
        <el-breadcrumb-item>捞绿方块（魔力棱镜）</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="content">
      <el-form
        :model="ruleForm"
        status-icon
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="ruleForm"
      >
        <el-form-item label="数量" prop="number" required>
          <el-input v-model="ruleForm.number" autocomplete="off" style="width: 200px"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" size="mini" @click="submitForm('ruleForm')"
            >保存</el-button
          >
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
  
<script>
export default {
  name: "GreenSquare",
  components: {},
  created() {},
  watch: {},
  data() {

    var validateNumber = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入数量"));
      } else {
        callback();
      }
    };

    return {
      id: this.$route.query.id,

      ruleForm: {
        number: "",
      },
      rules: {
        number: [{ validator: validateNumber, trigger: "blur" }],
      },
    };
  },

  methods: {
    /**
     * 提交表单
     */
     submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.request();
        } else {
          this.$message.error("请检查输入！");
          return false;
        }
      });
    },
    request() {
      
      const params = {
        orderId: this.id,
        boostingTask: this.ruleForm.number + '|' + "绿方块",
        progress:"jsons",
        businessType: "GatherGreenSquare"
      };
      this.$api.taskBoosting(params).then(
        (res) => {
          const body = res.data;
          if (body.code == "200") {
            this.$message.success("成功！");

          } else {
            this.$message.error(body.message);
          }
        }
        
      );
    },
  },
};
</script>
  
<style scoped lang="less">
.header {
  padding: 20px 100px 0 20px;
  height: 50px;
}
.content {
  padding: 0px 100px 0 20px;
  .ruleForm {
    width: 400px;
  }
}
</style>
  