  methods: {
    /**
     * 设置业务优先级
     */
    handleSetClass() {
      const params = this.setClassForm;
      this.$api.businessOrder(params).then(
        (res) => {
          const body = res.data;
          if (body.code == "200") {
            this.$message.success("成功！");

          } else {
            this.$message.error(body.message);
          }
        }
      );
    },
    handleDelete(boostingName) {
      const params = {
        orderId: this.id,
        boostingName: boostingName
      };
      this.$api.boostingDelete(params).then(
        (res) => {
          const body = res.data;
          if (body.code == "200") {
            this.$message.success("成功！");

          } else {
            this.$message.error(body.message);
          }
        }
      );
    },

    /**
     * 请求
     */
    requestBaseInfo() {
      const params = {
        orderId: this.id,
      };
      this.$api.getBaseInfo(params).then(
        (res) => {
          const body = res.data;
          this.baseInfoData = [];
          this.baseInfoData.push({
            playerAccount: body.nickName,
            class: body.gameLevel,
            QP: "/",
            stone: body.rockAtCurrent,
            fruitAtCurrent:body.fruitAtCurrent,

            id: body.uidOfFriend,
          });
          if (res.code == "200") {
            this.$message.success("成功！");

          } else {
            this.$message.error(body.message);
          }
        }
      );
    },
    requestBusinessProgress() {
      const params = {
        orderId: this.id,
      };
      this.$api.getBusinessProgress(params).then(
        (res) => {
          const body = res.data;
          this.signInInfoData = [res.data.signIn]
          this.boostingLevelsData = res.data.boostingLevels
          this.gatherBallsData = [res.data.gatherBalls]
          this.gatherDogFoodData = [res.data.gatherDogFood]
          this.gatherMaterialsData = res.data.gatherMaterials
          this.gatherGreenSquareData = [res.data.gatherGreenSquare]
          this.boostingEventsData = res.data.boostingEvents
          this.dailyInfoData = [res.data.dailyInfo]
          // this.boostingHuntingInfoData = res.data.boostingHuntingInfo
          this.skillInfoData = res.data.follower
          this.signInInfoFlag = this.signInInfoData == [] ? false : true
          this.boostingLevelsFlag = this.boostingLevelsData == [] ? false : true
          this.gatherBallsFlag = this.gatherBallsData == [] ? false : true
          this.gatherDogFoodFlag = this.gatherDogFoodData == [] ? false : true
          this.gatherMaterialsFlag = this.gatherMaterialsData == [] ? false : true
          this.gatherGreenSquareFlag = this.gatherGreenSquareData == [] ? false : true
          this.boostingEventsFlag = this.boostingEventsData == [] ? false : true
          this.dailyInfoFlag = this.dailyInfoData == [] ? false : true
          this.boostingHuntingInfoFlag = this.boostingHuntingInfoData == [] ? false : true
          this.skillInfoFlag = this.skillInfoData == [] ? false : true
          if (res.code == "200") {
            this.$message.success("成功！");

          } else {
            this.$message.error(body.message);
          }
        }
      );
