import http from './http'
export default {
  login: (params) => http.post('/user/login', params),
  register: (params) => http.post('/user/register', params),
  logout: (params) => http.post('/user/logout', params),

  /**
   * 新增订单
   * @param {*} params 
   * @returns 
   */
  addOrder: (params) => http.post('/order', params),
  /**
   * 修改订单
   * @param {*} params 
   * @returns 
   */
  modifyOrder: (params) => http.put('/order', params),
  /**
   * 获取订单列表
   * @param {*} params 
   * @returns 
   */
  getOrder: (params) => http.post('/order/search', params),
  /**
   * 中途结算
   * @param {Array} params 
   * @returns 
   */
  midSettle: (params) => http.get('/midSettle', params),
  /**
  * 交单结算
  * @param {Array} params 
  * @returns 
  */
  submitSettle: (params) => http.get('/submitSettle', params),
  /**
   * 修改订单状态
   * @param {*} params 
   * @returns 
   */
  changeOrderStatus: (params) => http.put('/order/status', params),

  /**
   * 搜索订单
   * @param {*} params 
   * @returns 
   */
  searchOrder: (params) => http.get('/order/search', params),


  /**
   * 设置代练内容
   */
  taskBoosting: (params) => http.post('/boosting', params),

  /**
   * 获取账号基本信息
   */
  getBaseInfo: (params) => http.get('/user/basic', params),

  /**
   * 获取代练进度
   */
  getBusinessProgress: (params) => http.get('/business/progressOverview', params),


  /**
   * 获取代练进度
   */
  boostingDelete: (params) => http.post('/boosting/delete', params),

  /**
   * 设置业务优先级
   */
  businessOrder: (params) => http.post('/business/order', params),


}

